import React, { useMemo } from "react";
import TabButton from "./TabButton";
import { ReactComponent as setup } from "assets/icons/Inventory/setup.svg";
// import { ReactComponent as basicIcon } from "assets/icons/Inventory/BasicDetails.svg";

import { ReactComponent as unit_name } from "assets/icons/Inventory/unit_name.svg";
import { ReactComponent as rooms_beds } from "assets/icons/Inventory/rooms_beds.svg";
import { ReactComponent as unit_package } from "assets/icons/Inventory/unit_package.svg";
import { ReactComponent as unit_profile } from "assets/icons/Inventory/unit_profile.svg";



import styles from "./TabButton/index.module.scss";

let tabList = [
  {
    status: "IN PROGRESS",
    icon: setup,
    name: "Inventory Set-Up",
  },
  {
    status: "UP NEXT",
    icon: unit_name,
    name: "Unit Naming",
  },
  {
    status: "UP NEXT",
    icon: rooms_beds,
    name: "Rooms & Beds",
  },
  {
    status: "UP NEXT",
    icon: unit_package,
    name: "Unit Packages",
  },
  {
    status: "UP NEXT",
    icon: unit_profile,
    name: "Unit Profile",
  },
  //   {
  //     status: "UP NEXT",
  //     icon: photoIcon,
  //     name: "Photos & Docs",
  //   },
  //   {
  //     status: "UP NEXT",
  //     name: "Company Details",
  //     icon: companyIcon,
  //   },
];

const Tabs = ({ activeTab, handleActiveTab }) => {
  let actived = false;
  return <div className={styles.addUnitTab}>{tabList?.map(({ icon, status, name }, i, arr) => {
    if (activeTab === i) {
      status = "IN PROGRESS";
      actived = true;
    } else if (!actived) status = "COMPLETED";
    return (
      <TabButton
        Icon={icon}
        name={name}
        status={status}
        key={name}
        isNotLast={i < arr.length - 1}
        handleActiveTab={() => (i < activeTab ? handleActiveTab(i) : {})}
      />
    );
  })}</div>;
};

export default Tabs;
